import React ,{useState}from 'react';
import './Createnote.css'
import Zoom from '@mui/material/Zoom';
import Fab from '@mui/material/Fab';
import AddIcon from '@mui/icons-material/Add';
import { RowingSharp, SettingsRemoteSharp } from '@material-ui/icons';

const Createnote = (props) => {
  
  const expand = () =>{
    setIsEspanded (true);
  }
  const [isExpanded,setIsEspanded]=useState(false);
  const [note,setNote] = useState({
    title :"",
    content : ""
  })
  const handleChange =(event) =>{
    const {name,value} = event.target;
    setNote ((prevNote) =>{
      return{
      ...prevNote,
      [name] : value
      };
    });
  };

const submitNote =(event)=>{
  event.preventDefault();
  props.onAdd(note);
  setNote({
    title:"",
    content:""
  })
  setIsEspanded(false);
  
}
console.log(note);

  
  return <div>
  <form className='create-note'>
    {isExpanded && <input type = "text" name='title' placeholder='title' 
    onChange={handleChange}
    value={note.title}
    />}
   <textarea name='content' placeholder='Take a note' 
   onClick={expand} onChange={handleChange} 
   rows = {isExpanded ? 3 : 1}
   value={note.content}
   
   />
   
   <Zoom in={isExpanded} >
     <Fab onClick={submitNote}>
       <AddIcon />

     </Fab>
   </Zoom>
  

  </form>
  </div>;
};

export default Createnote;
